import { useState } from "react";
import { useTranslation } from "react-i18next";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Loader2 } from "lucide-react";

interface BatchEditCouponStatusDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  selectedCodeIds: string[];
  onSuccess: () => void;
}

type CouponStatus = "available" | "used" | "expired";

export function BatchEditCouponStatusDialog({
  open,
  onOpenChange,
  selectedCodeIds,
  onSuccess,
}: BatchEditCouponStatusDialogProps) {
  const { t } = useTranslation();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [status, setStatus] = useState<CouponStatus>("available");

  const handleSubmit = async () => {
    if (selectedCodeIds.length === 0) return;
    
    setIsSubmitting(true);
    try {
      // Update coupon_codes table
      const { error: codeError } = await supabase
        .from("coupon_codes")
        .update({
          is_active: status !== "expired",
        })
        .in("id", selectedCodeIds);

      if (codeError) throw codeError;

      // Handle user_coupons updates based on status
      if (status === "used") {
        // Mark all user_coupons as used for selected codes
        const { error: userCouponError } = await supabase
          .from("user_coupons")
          .update({ used_at: new Date().toISOString() })
          .in("coupon_code_id", selectedCodeIds)
          .is("used_at", null);

        if (userCouponError) throw userCouponError;
      } else if (status === "available") {
        // Clear used_at for all user_coupons (reset to available)
        const { error: userCouponError } = await supabase
          .from("user_coupons")
          .update({ used_at: null })
          .in("coupon_code_id", selectedCodeIds);

        if (userCouponError) throw userCouponError;
      }
      // For "expired" status, we don't change used_at

      toast.success(t("admin.couponCode.batchUpdateSuccess", { count: selectedCodeIds.length }));
      onSuccess();
      onOpenChange(false);
    } catch (error) {
      console.error("Error batch updating coupon codes:", error);
      toast.error(t("admin.couponCode.batchUpdateError"));
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>{t("admin.couponCode.batchEditTitle")}</DialogTitle>
          <DialogDescription>
            {t("admin.couponCode.batchEditDescription", { count: selectedCodeIds.length })}
          </DialogDescription>
        </DialogHeader>
        <div className="space-y-4 py-4">
          <div className="space-y-2">
            <Label>{t("admin.couponCode.status")}</Label>
            <Select
              value={status}
              onValueChange={(value: CouponStatus) => setStatus(value)}
            >
              <SelectTrigger>
                <SelectValue placeholder={t("admin.couponCode.selectStatus")} />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="available">{t("admin.couponCode.statusAvailable")}</SelectItem>
                <SelectItem value="used">{t("admin.couponCode.statusUsed")}</SelectItem>
                <SelectItem value="expired">{t("admin.couponCode.statusExpired")}</SelectItem>
              </SelectContent>
            </Select>
            <p className="text-xs text-muted-foreground">
              {status === "available" && t("admin.couponCode.statusAvailableHint")}
              {status === "used" && t("admin.couponCode.statusUsedHint")}
              {status === "expired" && t("admin.couponCode.statusExpiredHint")}
            </p>
          </div>
        </div>
        <DialogFooter>
          <Button
            type="button"
            variant="outline"
            onClick={() => onOpenChange(false)}
            disabled={isSubmitting}
          >
            {t("common.cancel")}
          </Button>
          <Button onClick={handleSubmit} disabled={isSubmitting}>
            {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            {t("admin.couponCode.batchUpdate")}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
