import { useQuery, useQueryClient } from "@tanstack/react-query";
import { useParams, useNavigate, Link } from "react-router-dom";
import { supabase } from "@/integrations/supabase/client";
import { useTranslation } from "react-i18next";
import { useState, useMemo } from "react";

import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { Ticket, Calendar, Code, ArrowLeft, User, Search, Pencil, Trash2, CheckSquare, Square, History, Store } from "lucide-react";
import { format } from "date-fns";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import type { CouponTemplate } from "@/hooks/useCoupons";
import { useTemplateActivities, useTemplateUsageHistory } from "@/hooks/useCoupons";
import { ManageTemplateActivitiesDialog } from "@/components/admin/ManageTemplateActivitiesDialog";
import { EditCouponCodeDialog } from "@/components/admin/EditCouponCodeDialog";
import { BatchEditCouponStatusDialog } from "@/components/admin/BatchEditCouponStatusDialog";
import { Checkbox } from "@/components/ui/checkbox";
import { toast } from "sonner";

interface CouponCodeWithUser {
  id: string;
  code: string;
  template_id: string;
  usage_limit: number;
  used_count: number;
  is_active: boolean;
  created_at: string;
  updated_at: string;
  user_coupons: Array<{
    id: string;
    user_id: string;
    claimed_at: string;
    used_at: string | null;
    source: string | null;
    profiles: {
      id: string;
      display_name: string;
      avatar_url: string | null;
    };
    used_by_merchant?: {
      display_name: string;
      avatar_url: string | null;
    };
    used_at_restaurant?: {
      name: string;
      name_en: string | null;
      name_pt: string | null;
    };
  }>;
}

const AdminCouponDetail = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const queryClient = useQueryClient();
  
  // 搜索和过滤状态
  const [searchQuery, setSearchQuery] = useState("");
  const [statusFilter, setStatusFilter] = useState<"all" | "active" | "inactive">("all");
  const [usageFilter, setUsageFilter] = useState<"all" | "unclaimed" | "claimed" | "used">("all");
  const [sourceFilter, setSourceFilter] = useState<"all" | "share" | "other">("all");
  
  // 编辑和删除对话框状态
  const [editingCode, setEditingCode] = useState<CouponCodeWithUser | null>(null);
  const [deletingCodeId, setDeletingCodeId] = useState<string | null>(null);
  const [isDeleting, setIsDeleting] = useState(false);
  
  // 批量选择状态
  const [selectedCodes, setSelectedCodes] = useState<Set<string>>(new Set());
  const [showBatchEditDialog, setShowBatchEditDialog] = useState(false);
  const [showBatchDeleteDialog, setShowBatchDeleteDialog] = useState(false);
  const [isBatchDeleting, setIsBatchDeleting] = useState(false);

  // 获取优惠券模板信息
  const { data: template, isLoading: templateLoading } = useQuery({
    queryKey: ["couponTemplate", id],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("coupon_templates")
        .select("*")
        .eq("id", id)
        .single();

      if (error) throw error;
      return data as CouponTemplate;
    },
    enabled: !!id,
  });

  // 获取关联的活动列表
  const { data: templateActivities, isLoading: activitiesLoading } = useTemplateActivities(id!);

  // 获取使用历史记录
  const { data: usageHistory, isLoading: historyLoading } = useTemplateUsageHistory(id!);

  // 获取此模板生成的所有子优惠券及用户信息
  const { data: couponCodes, isLoading: codesLoading } = useQuery({
    queryKey: ["templateCouponCodes", id],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("coupon_codes")
        .select(`
          *,
          user_coupons(
            id,
            user_id,
            claimed_at,
            used_at,
            source
          )
        `)
        .eq("template_id", id)
        .order("created_at", { ascending: false });

      if (error) throw error;
      
      // 获取所有 coupon_code_id 用于查询使用历史
      const codeIds = (data || []).map((c: any) => c.id);
      
      // 获取使用历史以获取核销商家信息
      const { data: usageHistoryData } = await supabase
        .from("coupon_usage_history")
        .select("user_coupon_id, used_by_user_id, restaurant_id")
        .in("coupon_code_id", codeIds);
      
      // 获取核销商家的 profile 信息
      const merchantUserIds = [...new Set((usageHistoryData || []).map(h => h.used_by_user_id))];
      const { data: merchantProfiles } = await supabase
        .from("profiles")
        .select("id, display_name, avatar_url")
        .in("id", merchantUserIds);
      
      const merchantProfilesMap = (merchantProfiles || []).reduce((acc, p) => {
        acc[p.id] = { display_name: p.display_name, avatar_url: p.avatar_url };
        return acc;
      }, {} as Record<string, { display_name: string; avatar_url: string | null }>);
      
      // 获取餐厅信息
      const restaurantIds = [...new Set((usageHistoryData || []).filter(h => h.restaurant_id).map(h => h.restaurant_id as string))];
      let restaurantsMap: Record<string, { name: string; name_en: string | null; name_pt: string | null }> = {};
      if (restaurantIds.length > 0) {
        const { data: restaurants } = await supabase
          .from("restaurants")
          .select("id, name, name_en, name_pt")
          .in("id", restaurantIds);
        restaurantsMap = (restaurants || []).reduce((acc, r) => {
          acc[r.id] = { name: r.name, name_en: r.name_en, name_pt: r.name_pt };
          return acc;
        }, {} as Record<string, { name: string; name_en: string | null; name_pt: string | null }>);
      }
      
      // 创建 user_coupon_id 到使用历史的映射
      const usageByUserCouponId = (usageHistoryData || []).reduce((acc, h) => {
        if (h.user_coupon_id) {
          acc[h.user_coupon_id] = {
            used_by_merchant: merchantProfilesMap[h.used_by_user_id] || null,
            used_at_restaurant: h.restaurant_id ? restaurantsMap[h.restaurant_id] || null : null,
          };
        }
        return acc;
      }, {} as Record<string, { used_by_merchant: { display_name: string; avatar_url: string | null } | null; used_at_restaurant: { name: string; name_en: string | null; name_pt: string | null } | null }>);
      
      // Fetch profile information for each user_coupon
      const codesWithProfiles = await Promise.all(
        (data || []).map(async (code: any) => {
          const userCouponsWithProfiles = await Promise.all(
            (code.user_coupons || []).map(async (userCoupon: any) => {
              // 如果 user_id 为 null（直接核销的情况），不查询 profile
              if (!userCoupon.user_id) {
                return {
                  ...userCoupon,
                  profiles: { id: "", display_name: "Unknown", avatar_url: null },
                  ...usageByUserCouponId[userCoupon.id],
                };
              }
              
              const { data: profile } = await supabase
                .from("profiles")
                .select("id, display_name, avatar_url")
                .eq("id", userCoupon.user_id)
                .maybeSingle();
              
              return {
                ...userCoupon,
                profiles: profile || { id: userCoupon.user_id, display_name: "Unknown", avatar_url: null },
                ...usageByUserCouponId[userCoupon.id],
              };
            })
          );
          
          return {
            ...code,
            user_coupons: userCouponsWithProfiles
          };
        })
      );
      
      return codesWithProfiles as CouponCodeWithUser[];
    },
    enabled: !!id,
  });

  // 过滤优惠券 - Must be before any conditional returns
  const filteredCouponCodes = useMemo(() => {
    if (!couponCodes) return [];
    
    return couponCodes.filter((code) => {
      // 搜索过滤：优惠券代码或用户名
      const searchLower = searchQuery.toLowerCase();
      const matchesSearch = 
        searchQuery === "" ||
        code.code.toLowerCase().includes(searchLower) ||
        code.user_coupons.some((uc) => 
          uc.profiles.display_name.toLowerCase().includes(searchLower)
        );
      
      // 状态过滤
      const matchesStatus = 
        statusFilter === "all" ||
        (statusFilter === "active" && code.is_active) ||
        (statusFilter === "inactive" && !code.is_active);
      
      // 使用状态过滤
      const matchesUsage = 
        usageFilter === "all" ||
        (usageFilter === "unclaimed" && code.user_coupons.length === 0) ||
        (usageFilter === "claimed" && code.user_coupons.length > 0 && code.user_coupons.every(uc => !uc.used_at)) ||
        (usageFilter === "used" && code.user_coupons.some(uc => uc.used_at));
      
      // 来源过滤
      const matchesSource = 
        sourceFilter === "all" ||
        (sourceFilter === "share" && code.user_coupons.some(uc => uc.source === "share")) ||
        (sourceFilter === "other" && code.user_coupons.some(uc => uc.source !== "share"));
      
      return matchesSearch && matchesStatus && matchesUsage && matchesSource;
    });
  }, [couponCodes, searchQuery, statusFilter, usageFilter, sourceFilter]);

  const totalGenerated = couponCodes?.length || 0;
  const totalUsed = couponCodes?.reduce((sum, code) => sum + code.used_count, 0) || 0;
  const totalClaimed = couponCodes?.reduce((sum, code) => sum + code.user_coupons.length, 0) || 0;

  // 批量选择功能
  const handleToggleSelect = (codeId: string) => {
    setSelectedCodes(prev => {
      const newSet = new Set(prev);
      if (newSet.has(codeId)) {
        newSet.delete(codeId);
      } else {
        newSet.add(codeId);
      }
      return newSet;
    });
  };

  const handleSelectAll = () => {
    if (selectedCodes.size === filteredCouponCodes.length) {
      setSelectedCodes(new Set());
    } else {
      setSelectedCodes(new Set(filteredCouponCodes.map(code => code.id)));
    }
  };

  const handleBatchEditSuccess = () => {
    setSelectedCodes(new Set());
    queryClient.invalidateQueries({ queryKey: ["templateCouponCodes", id] });
  };

  const handleBatchDelete = async () => {
    if (selectedCodes.size === 0) return;
    
    setIsBatchDeleting(true);
    try {
      const selectedIds = Array.from(selectedCodes);
      
      // 先删除关联的 user_coupons
      const { error: userCouponsError } = await supabase
        .from("user_coupons")
        .delete()
        .in("coupon_code_id", selectedIds);

      if (userCouponsError) throw userCouponsError;

      // 再删除 coupon_codes
      const { error } = await supabase
        .from("coupon_codes")
        .delete()
        .in("id", selectedIds);

      if (error) throw error;

      toast.success(t("admin.couponCode.batchDeleteSuccess", { count: selectedIds.length }));
      setSelectedCodes(new Set());
      queryClient.invalidateQueries({ queryKey: ["templateCouponCodes", id] });
      setShowBatchDeleteDialog(false);
    } catch (error) {
      console.error("Error batch deleting coupon codes:", error);
      toast.error(t("admin.couponCode.batchDeleteError"));
    } finally {
      setIsBatchDeleting(false);
    }
  };

  const handleDeleteCouponCode = async (codeId: string) => {
    setIsDeleting(true);
    try {
      // 先删除关联的 user_coupons
      const { error: userCouponsError } = await supabase
        .from("user_coupons")
        .delete()
        .eq("coupon_code_id", codeId);

      if (userCouponsError) throw userCouponsError;

      // 再删除 coupon_code
      const { error } = await supabase
        .from("coupon_codes")
        .delete()
        .eq("id", codeId);

      if (error) throw error;

      toast.success(t("admin.couponDetail.deleteSuccess"));
      queryClient.invalidateQueries({ queryKey: ["templateCouponCodes", id] });
      setDeletingCodeId(null);
    } catch (error) {
      console.error("Error deleting coupon code:", error);
      toast.error(t("admin.couponDetail.deleteError"));
    } finally {
      setIsDeleting(false);
    }
  };

  if (templateLoading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center py-12">{t("admin.couponDetail.loading")}</div>
      </div>
    );
  }

  if (!template) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center py-12">
          <p className="text-muted-foreground mb-4">{t("admin.couponDetail.templateNotFound")}</p>
          <Button onClick={() => navigate("/admin/coupons")}>{t("admin.couponDetail.backToList")}</Button>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="container mx-auto px-4 py-8">
        <div className="mb-6">
          <Button
            variant="ghost"
            onClick={() => navigate("/admin/coupons")}
            className="mb-4"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            {t("admin.couponDetail.backToList")}
          </Button>
          <h1 className="text-3xl font-bold flex items-center gap-2">
            <Ticket className="w-8 h-8" />
            {t("admin.couponDetail.title")}
          </h1>
        </div>

        <div className="space-y-6">
          {/* 模板基本信息 */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">{t("admin.couponDetail.templateInfo")}</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex-1">
                  <p className="text-sm text-muted-foreground mb-2">{t("admin.couponDetail.linkedActivities")}</p>
                  {activitiesLoading ? (
                    <p className="text-sm">{t("admin.couponDetail.loading")}</p>
                  ) : templateActivities && templateActivities.length > 0 ? (
                    <div className="space-y-2">
                      {templateActivities.map((ta: any) => (
                        <Link
                          key={ta.activity_id}
                          to={`/activity/${ta.activity_id}`}
                          className="block text-primary hover:underline"
                        >
                          {getLocalizedField(ta.activities, "title")}
                        </Link>
                      ))}
                    </div>
                  ) : (
                    <p className="text-sm text-muted-foreground">{t("admin.couponDetail.noLinkedActivities")}</p>
                  )}
                </div>
                <div className="flex gap-2 items-start">
                  <ManageTemplateActivitiesDialog templateId={id!} />
                  {!template.visible && <Badge variant="secondary">{t("admin.couponDetail.hidden")}</Badge>}
                  {!template.is_active && <Badge variant="secondary">{t("admin.couponDetail.inactive")}</Badge>}
                  {template.is_active && <Badge>{t("admin.couponDetail.active")}</Badge>}
                </div>
              </div>

              <Separator />

              <div>
                <p className="text-sm text-muted-foreground mb-2">{t("admin.couponDetail.titleLabel")}</p>
                <div className="space-y-1">
                  <p>
                    <span className="text-xs text-muted-foreground">{t("admin.chinese")}：</span>
                    {template.title_zh}
                  </p>
                  <p>
                    <span className="text-xs text-muted-foreground">{t("admin.english")}：</span>
                    {template.title_en}
                  </p>
                  <p>
                    <span className="text-xs text-muted-foreground">{t("admin.portuguese")}：</span>
                    {template.title_pt}
                  </p>
                </div>
              </div>

              {(template.description_zh ||
                template.description_en ||
                template.description_pt) && (
                <>
                  <Separator />
                  <div>
                    <p className="text-sm text-muted-foreground mb-2">{t("admin.couponDetail.descriptionLabel")}</p>
                    <div className="space-y-1 text-sm">
                      {template.description_zh && (
                        <p>
                          <span className="text-xs text-muted-foreground">{t("admin.chinese")}：</span>
                          {template.description_zh}
                        </p>
                      )}
                      {template.description_en && (
                        <p>
                          <span className="text-xs text-muted-foreground">{t("admin.english")}：</span>
                          {template.description_en}
                        </p>
                      )}
                      {template.description_pt && (
                        <p>
                          <span className="text-xs text-muted-foreground">{t("admin.portuguese")}：</span>
                          {template.description_pt}
                        </p>
                      )}
                    </div>
                  </div>
                </>
              )}

              <Separator />

              <div className="grid grid-cols-2 md:grid-cols-3 gap-4 text-sm">
                <div>
                  <p className="text-muted-foreground">{t("admin.couponDetail.discountType")}</p>
                  <p className="font-medium">
                    {template.discount_type === "percentage" ? t("admin.couponDetail.percentageDiscount") : t("admin.couponDetail.fixedAmount")}
                  </p>
                </div>
                <div>
                  <p className="text-muted-foreground">{t("admin.couponDetail.discountValue")}</p>
                  <p className="font-medium text-primary text-lg">
                    {template.discount_type === "percentage"
                      ? `${template.discount_value}%`
                      : `€${template.discount_value}`}
                  </p>
                </div>
                {template.min_order_amount && template.min_order_amount > 0 && (
                  <div>
                    <p className="text-muted-foreground">{t("admin.couponDetail.minOrderAmount")}</p>
                    <p className="font-medium">€{template.min_order_amount}</p>
                  </div>
                )}
                {template.max_discount_amount && template.max_discount_amount > 0 && (
                  <div>
                    <p className="text-muted-foreground">{t("admin.couponDetail.maxDiscountAmount")}</p>
                    <p className="font-medium">€{template.max_discount_amount}</p>
                  </div>
                )}
                <div>
                  <p className="text-muted-foreground">{t("admin.couponDetail.generated")}</p>
                  <p className="font-medium">{totalGenerated} {t("admin.couponDetail.units")}</p>
                </div>
                <div>
                  <p className="text-muted-foreground">{t("admin.couponDetail.claimed")}</p>
                  <p className="font-medium">{totalClaimed} {t("admin.couponDetail.times")}</p>
                </div>
                <div>
                  <p className="text-muted-foreground">{t("admin.couponDetail.used")}</p>
                  <p className="font-medium">{totalUsed} {t("admin.couponDetail.times")}</p>
                </div>
              </div>

              <Separator />

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div className="flex items-center gap-2">
                  <Calendar className="h-4 w-4 text-muted-foreground" />
                  <div>
                    <p className="text-muted-foreground">{t("admin.couponDetail.validPeriod")}</p>
                    <p className="font-medium">
                      {format(new Date(template.valid_from), "yyyy-MM-dd")}
                      {template.valid_until
                        ? ` - ${format(new Date(template.valid_until), "yyyy-MM-dd")}`
                        : ` - ${t("admin.couponDetail.unlimited")}`}
                    </p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* 已生成的子优惠券列表 */}
          <Card>
            <CardHeader>
              <div className="space-y-4">
                <CardTitle className="text-lg flex items-center gap-2">
                  <Code className="h-4 w-4" />
                  {t("admin.couponDetail.generatedCoupons")} ({totalGenerated})
                </CardTitle>
                
                {/* 搜索和过滤 */}
                {couponCodes && couponCodes.length > 0 && (
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                      <Input
                        placeholder={t("admin.couponDetail.searchPlaceholder")}
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="pl-9"
                      />
                    </div>
                    
                    <Select value={statusFilter} onValueChange={(value: any) => setStatusFilter(value)}>
                      <SelectTrigger>
                        <SelectValue placeholder={t("admin.couponDetail.statusFilter")} />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">{t("admin.couponDetail.allStatus")}</SelectItem>
                        <SelectItem value="active">{t("admin.couponDetail.statusActive")}</SelectItem>
                        <SelectItem value="inactive">{t("admin.couponDetail.statusInactive")}</SelectItem>
                      </SelectContent>
                    </Select>
                    
                    <Select value={usageFilter} onValueChange={(value: any) => setUsageFilter(value)}>
                      <SelectTrigger>
                        <SelectValue placeholder={t("admin.couponDetail.usageFilter")} />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">{t("admin.couponDetail.usageAll")}</SelectItem>
                        <SelectItem value="unclaimed">{t("admin.couponDetail.usageUnclaimed")}</SelectItem>
                        <SelectItem value="claimed">{t("admin.couponDetail.usageClaimed")}</SelectItem>
                        <SelectItem value="used">{t("admin.couponDetail.usageUsed")}</SelectItem>
                      </SelectContent>
                    </Select>
                    
                    <Select value={sourceFilter} onValueChange={(value: any) => setSourceFilter(value)}>
                      <SelectTrigger>
                        <SelectValue placeholder={t("admin.couponDetail.sourceFilter")} />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="all">{t("admin.couponDetail.sourceAll")}</SelectItem>
                        <SelectItem value="share">{t("admin.couponDetail.sourceShare")}</SelectItem>
                        <SelectItem value="other">{t("admin.couponDetail.sourceOther")}</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                )}
              </div>
            </CardHeader>
            <CardContent>
              {codesLoading ? (
                <p className="text-sm text-muted-foreground">{t("admin.couponDetail.loading")}</p>
              ) : !couponCodes || couponCodes.length === 0 ? (
                <p className="text-sm text-muted-foreground">
                  {t("admin.couponDetail.noCouponsYet")}
                </p>
              ) : filteredCouponCodes.length === 0 ? (
                <p className="text-sm text-muted-foreground">
                  {t("admin.couponDetail.noMatchingCoupons")}
                </p>
              ) : (
                <div className="space-y-4">
                  {/* 批量操作栏 */}
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                      <div className="flex items-center gap-2">
                        <Checkbox
                          id="selectAll"
                          checked={selectedCodes.size === filteredCouponCodes.length && filteredCouponCodes.length > 0}
                          onCheckedChange={handleSelectAll}
                        />
                        <label htmlFor="selectAll" className="text-sm cursor-pointer">
                          {t("admin.couponCode.selectAll")}
                        </label>
                      </div>
                      {selectedCodes.size > 0 && (
                        <span className="text-sm text-muted-foreground">
                          {t("admin.couponCode.selectedCount", { count: selectedCodes.size })}
                        </span>
                      )}
                    </div>
                    <div className="flex items-center gap-2">
                      {selectedCodes.size > 0 && (
                        <>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => setShowBatchEditDialog(true)}
                          >
                            {t("admin.couponCode.batchEditStatus")}
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => setShowBatchDeleteDialog(true)}
                          >
                            {t("admin.couponCode.batchDelete")}
                          </Button>
                        </>
                      )}
                      <span className="text-sm text-muted-foreground">
                        {filteredCouponCodes.length} / {totalGenerated}
                      </span>
                    </div>
                  </div>
                  {filteredCouponCodes.map((code) => (
                    <div
                      key={code.id}
                      className={`rounded-lg border bg-card p-4 ${selectedCodes.has(code.id) ? 'ring-2 ring-primary' : ''}`}
                    >
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex items-center gap-3">
                          <Checkbox
                            checked={selectedCodes.has(code.id)}
                            onCheckedChange={() => handleToggleSelect(code.id)}
                          />
                          <div className="w-12 h-12 rounded bg-primary/10 flex items-center justify-center">
                            <Ticket className="h-6 w-6 text-primary" />
                          </div>
                          <div>
                            <code className="font-mono font-semibold text-base">
                              {code.code}
                            </code>
                            <p className="text-xs text-muted-foreground mt-1">
                              {t("admin.couponDetail.generatedAt")}：{format(new Date(code.created_at), "yyyy-MM-dd HH:mm")}
                            </p>
                            <div className="flex items-center gap-4 mt-1">
                              <p className="text-xs text-muted-foreground">
                                {t("admin.couponDetail.usage")}：{code.used_count} / {code.usage_limit}
                              </p>
                              <Badge variant={code.is_active ? "default" : "secondary"}>
                                {code.is_active ? t("admin.couponDetail.activated") : t("admin.couponDetail.deactivated")}
                              </Badge>
                            </div>
                          </div>
                        </div>
                        <div className="flex items-center gap-2">
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => setEditingCode(code)}
                            title={t("admin.couponDetail.edit")}
                          >
                            <Pencil className="h-4 w-4" />
                          </Button>
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => setDeletingCodeId(code.id)}
                            title={t("admin.couponDetail.delete")}
                          >
                            <Trash2 className="h-4 w-4 text-destructive" />
                          </Button>
                        </div>
                      </div>

                      {/* 用户信息 */}
                      {code.user_coupons.length > 0 ? (
                        <div className="space-y-2 pt-3 border-t">
                          <p className="text-sm font-medium text-muted-foreground mb-2">
                            {t("admin.couponDetail.claimedUsers")}
                          </p>
                          {code.user_coupons.map((userCoupon) => (
                            <div
                              key={userCoupon.id}
                              className="flex items-center justify-between p-3 rounded-md bg-secondary/20"
                            >
                              <div className="flex items-center gap-3">
                                <Avatar className="h-10 w-10">
                                  <AvatarImage src={userCoupon.profiles.avatar_url || undefined} />
                                  <AvatarFallback>
                                    <User className="h-5 w-5" />
                                  </AvatarFallback>
                                </Avatar>
                                <div>
                                  <p className="font-medium">{userCoupon.profiles.display_name}</p>
                                  <p className="text-xs text-muted-foreground">
                                    {t("admin.couponDetail.claimedAt")}：{format(new Date(userCoupon.claimed_at), "yyyy-MM-dd HH:mm")}
                                  </p>
                                  {userCoupon.source && (
                                    <p className="text-xs text-muted-foreground">
                                      {t("admin.couponDetail.source")}：{userCoupon.source === "share" ? t("admin.couponDetail.sourceShare") : userCoupon.source}
                                    </p>
                                  )}
                                </div>
                              </div>
                              <div className="flex flex-col items-end gap-1">
                                <Badge variant={userCoupon.used_at ? "default" : "secondary"}>
                                  {userCoupon.used_at
                                    ? `${t("admin.couponDetail.usedAt")} ${format(new Date(userCoupon.used_at), "MM-dd HH:mm")}`
                                    : t("admin.couponDetail.unused")}
                                </Badge>
                                {userCoupon.used_at && userCoupon.used_by_merchant && (
                                  <div className="flex items-center gap-1.5 text-xs text-muted-foreground">
                                    <Store className="h-3 w-3" />
                                    <span>{t("admin.couponDetail.verifiedBy")}：{userCoupon.used_by_merchant.display_name}</span>
                                    {userCoupon.used_at_restaurant && (
                                      <span className="text-primary">
                                        @ {getLocalizedField(userCoupon.used_at_restaurant, "name")}
                                      </span>
                                    )}
                                  </div>
                                )}
                              </div>
                            </div>
                          ))}
                        </div>
                      ) : (
                        <p className="text-sm text-muted-foreground pt-3 border-t">
                          {t("admin.couponDetail.noClaims")}
                        </p>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>

          {/* 使用历史记录 */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg flex items-center gap-2">
                <History className="h-4 w-4" />
                {t("admin.couponDetail.usageHistory")} ({usageHistory?.length || 0})
              </CardTitle>
            </CardHeader>
            <CardContent>
              {historyLoading ? (
                <p className="text-muted-foreground text-center py-4">{t("admin.couponDetail.loading")}</p>
              ) : !usageHistory || usageHistory.length === 0 ? (
                <p className="text-muted-foreground text-center py-4">{t("admin.couponDetail.noUsageHistory")}</p>
              ) : (
                <div className="space-y-3">
                  {usageHistory.map((history: any) => (
                    <div key={history.id} className="flex items-start gap-4 p-4 border rounded-lg">
                      <Avatar className="h-10 w-10">
                        {history.used_by_profile?.avatar_url ? (
                          <AvatarImage src={history.used_by_profile.avatar_url} />
                        ) : null}
                        <AvatarFallback>
                          <User className="h-5 w-5" />
                        </AvatarFallback>
                      </Avatar>
                      <div className="flex-1 space-y-1">
                        <div className="flex items-center gap-2 flex-wrap">
                          <span className="font-medium">
                            {history.used_by_profile?.display_name || t("common.unknown")}
                          </span>
                          <span className="text-muted-foreground">{t("admin.couponDetail.verifiedCoupon")}</span>
                          {history.coupon_code && (
                            <Badge variant="outline" className="font-mono text-xs">
                              {history.coupon_code}
                            </Badge>
                          )}
                        </div>
                        <div className="flex items-center gap-4 text-sm text-muted-foreground flex-wrap">
                          <span>{format(new Date(history.used_at), "yyyy-MM-dd HH:mm:ss")}</span>
                          {history.restaurant && (
                            <span className="flex items-center gap-1">
                              <Store className="h-3 w-3" />
                              {getLocalizedField(history.restaurant, "name")}
                            </span>
                          )}
                        </div>
                        {history.notes && (
                          <p className="text-sm text-muted-foreground mt-1">
                            {t("admin.couponDetail.notes")}: {history.notes}
                          </p>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>

      {/* 编辑对话框 */}
      {editingCode && (
        <EditCouponCodeDialog
          open={!!editingCode}
          onOpenChange={(open) => !open && setEditingCode(null)}
          couponCode={editingCode}
          onSuccess={() => {
            queryClient.invalidateQueries({ queryKey: ["templateCouponCodes", id] });
          }}
        />
      )}

      {/* 批量编辑对话框 */}
      <BatchEditCouponStatusDialog
        open={showBatchEditDialog}
        onOpenChange={setShowBatchEditDialog}
        selectedCodeIds={Array.from(selectedCodes)}
        onSuccess={handleBatchEditSuccess}
      />

      {/* 删除确认对话框 */}
      <AlertDialog open={!!deletingCodeId} onOpenChange={(open) => !open && setDeletingCodeId(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("admin.couponDetail.deleteConfirmTitle")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("admin.couponDetail.deleteConfirmDesc")}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel disabled={isDeleting}>{t("common.cancel")}</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => deletingCodeId && handleDeleteCouponCode(deletingCodeId)}
              disabled={isDeleting}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
            >
              {isDeleting ? t("admin.couponDetail.deleting") : t("admin.couponDetail.confirmDelete")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>

      {/* 批量删除确认对话框 */}
      <AlertDialog open={showBatchDeleteDialog} onOpenChange={setShowBatchDeleteDialog}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("admin.couponCode.batchDeleteTitle")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("admin.couponCode.batchDeleteDescription", { count: selectedCodes.size })}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel disabled={isBatchDeleting}>{t("common.cancel")}</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleBatchDelete}
              disabled={isBatchDeleting}
              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
            >
              {isBatchDeleting ? t("common.deleting") : t("common.delete")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </>
  );
};

export default AdminCouponDetail;
