import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useForm } from "react-hook-form";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Loader2 } from "lucide-react";

interface EditCouponCodeDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  couponCode: {
    id: string;
    code: string;
    usage_limit: number;
    is_active: boolean;
    user_coupons?: Array<{
      id: string;
      used_at: string | null;
    }>;
  };
  onSuccess: () => void;
}

type CouponStatus = "available" | "used" | "expired";

interface FormData {
  usage_limit: number;
  status: CouponStatus;
}

export function EditCouponCodeDialog({
  open,
  onOpenChange,
  couponCode,
  onSuccess,
}: EditCouponCodeDialogProps) {
  const { t } = useTranslation();
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Determine initial status based on coupon state
  const getInitialStatus = (): CouponStatus => {
    if (!couponCode.is_active) return "expired";
    const hasUsedCoupons = couponCode.user_coupons?.some(uc => uc.used_at);
    if (hasUsedCoupons) return "used";
    return "available";
  };

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    reset,
    formState: { errors },
  } = useForm<FormData>({
    defaultValues: {
      usage_limit: couponCode.usage_limit,
      status: getInitialStatus(),
    },
  });

  // Reset form when couponCode changes
  useEffect(() => {
    reset({
      usage_limit: couponCode.usage_limit,
      status: getInitialStatus(),
    });
  }, [couponCode, reset]);

  const currentStatus = watch("status");

  const onSubmit = async (data: FormData) => {
    setIsSubmitting(true);
    try {
      // Update coupon_codes table
      const { error: codeError } = await supabase
        .from("coupon_codes")
        .update({
          usage_limit: data.usage_limit,
          is_active: data.status !== "expired",
        })
        .eq("id", couponCode.id);

      if (codeError) throw codeError;

      // Handle user_coupons updates based on status
      if (couponCode.user_coupons && couponCode.user_coupons.length > 0) {
        if (data.status === "used") {
          // Mark all user_coupons as used
          const { error: userCouponError } = await supabase
            .from("user_coupons")
            .update({ used_at: new Date().toISOString() })
            .eq("coupon_code_id", couponCode.id)
            .is("used_at", null);

          if (userCouponError) throw userCouponError;
        } else if (data.status === "available") {
          // Clear used_at for all user_coupons (reset to available)
          const { error: userCouponError } = await supabase
            .from("user_coupons")
            .update({ used_at: null })
            .eq("coupon_code_id", couponCode.id);

          if (userCouponError) throw userCouponError;
        }
        // For "expired" status, we don't change used_at
      }

      toast.success(t("admin.couponCode.updateSuccess"));
      onSuccess();
      onOpenChange(false);
    } catch (error) {
      console.error("Error updating coupon code:", error);
      toast.error(t("admin.couponCode.updateError"));
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>{t("admin.couponCode.editTitle")}</DialogTitle>
          <DialogDescription>
            {t("admin.couponCode.editDescription")}: <code className="font-mono font-semibold">{couponCode.code}</code>
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="usage_limit">{t("admin.couponCode.usageLimit")}</Label>
            <Input
              id="usage_limit"
              type="number"
              min="1"
              {...register("usage_limit", {
                required: t("admin.couponCode.usageLimitRequired"),
                min: { value: 1, message: t("admin.couponCode.usageLimitMin") },
                valueAsNumber: true,
              })}
            />
            {errors.usage_limit && (
              <p className="text-sm text-destructive">{errors.usage_limit.message}</p>
            )}
            <p className="text-xs text-muted-foreground">
              {t("admin.couponCode.usageLimitHint")}
            </p>
          </div>

          <div className="space-y-2">
            <Label>{t("admin.couponCode.status")}</Label>
            <Select
              value={currentStatus}
              onValueChange={(value: CouponStatus) => setValue("status", value)}
            >
              <SelectTrigger>
                <SelectValue placeholder={t("admin.couponCode.selectStatus")} />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="available">{t("admin.couponCode.statusAvailable")}</SelectItem>
                <SelectItem value="used">{t("admin.couponCode.statusUsed")}</SelectItem>
                <SelectItem value="expired">{t("admin.couponCode.statusExpired")}</SelectItem>
              </SelectContent>
            </Select>
            <p className="text-xs text-muted-foreground">
              {currentStatus === "available" && t("admin.couponCode.statusAvailableHint")}
              {currentStatus === "used" && t("admin.couponCode.statusUsedHint")}
              {currentStatus === "expired" && t("admin.couponCode.statusExpiredHint")}
            </p>
          </div>

          <DialogFooter>
            <Button
              type="button"
              variant="outline"
              onClick={() => onOpenChange(false)}
              disabled={isSubmitting}
            >
              {t("common.cancel")}
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              {t("common.save")}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
