import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Checkbox } from "@/components/ui/checkbox";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Loader2, Link, Unlink } from "lucide-react";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { 
  useTemplateActivities, 
  useAddTemplateActivity, 
  useRemoveTemplateActivity 
} from "@/hooks/useCoupons";
import { format } from "date-fns";

interface ManageTemplateActivitiesDialogProps {
  templateId: string;
  children?: React.ReactNode;
}

export const ManageTemplateActivitiesDialog = ({ 
  templateId, 
  children 
}: ManageTemplateActivitiesDialogProps) => {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const [open, setOpen] = useState(false);

  // 获取当前关联的活动
  const { data: templateActivities, isLoading: loadingTemplateActivities } = 
    useTemplateActivities(templateId);

  // 获取所有可见的活动
  const { data: allActivities, isLoading: loadingAllActivities } = useQuery({
    queryKey: ["allActivitiesForCoupon"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("activities")
        .select("id, title_zh, title_en, title_pt, start_date, end_date, visible")
        .eq("visible", true)
        .order("start_date", { ascending: false });

      if (error) throw error;
      return data;
    },
    enabled: open,
  });

  const addTemplateActivity = useAddTemplateActivity();
  const removeTemplateActivity = useRemoveTemplateActivity();

  const linkedActivityIds = new Set(
    templateActivities?.map(ta => ta.activity_id) || []
  );

  const handleToggleActivity = async (activityId: string, isLinked: boolean) => {
    if (isLinked) {
      await removeTemplateActivity.mutateAsync({
        templateId,
        activityId,
      });
    } else {
      await addTemplateActivity.mutateAsync({
        templateId,
        activityId,
      });
    }
  };

  const isLoading = loadingTemplateActivities || loadingAllActivities;
  const isPending = addTemplateActivity.isPending || removeTemplateActivity.isPending;

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {children || (
          <Button variant="outline" size="sm">
            <Link className="w-4 h-4 mr-2" />
            管理关联活动
          </Button>
        )}
      </DialogTrigger>
      <DialogContent className="max-w-2xl max-h-[80vh] flex flex-col">
        <DialogHeader>
          <DialogTitle>管理关联活动</DialogTitle>
          <DialogDescription>
            选择要关联到此优惠券模板的活动。一个优惠券可以关联多个活动。
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="flex-1 pr-4">
          {isLoading ? (
            <div className="flex justify-center py-8">
              <Loader2 className="w-6 h-6 animate-spin" />
            </div>
          ) : (
            <div className="space-y-2">
              {allActivities && allActivities.length > 0 ? (
                allActivities.map((activity) => {
                  const isLinked = linkedActivityIds.has(activity.id);
                  return (
                    <div
                      key={activity.id}
                      className="flex items-center justify-between p-3 border rounded-lg hover:bg-muted/50 transition-colors"
                    >
                      <div className="flex items-center gap-3 flex-1">
                        <Checkbox
                          checked={isLinked}
                          onCheckedChange={() => handleToggleActivity(activity.id, isLinked)}
                          disabled={isPending}
                        />
                        <div className="flex-1">
                          <p className="font-medium">
                            {getLocalizedField(activity, "title")}
                          </p>
                          <p className="text-xs text-muted-foreground">
                            {format(new Date(activity.start_date), "yyyy-MM-dd")}
                            {activity.end_date && ` ~ ${format(new Date(activity.end_date), "yyyy-MM-dd")}`}
                          </p>
                        </div>
                      </div>
                      {isLinked && (
                        <Badge variant="secondary" className="ml-2">
                          <Link className="w-3 h-3 mr-1" />
                          已关联
                        </Badge>
                      )}
                    </div>
                  );
                })
              ) : (
                <div className="text-center py-8 text-muted-foreground">
                  暂无可关联的活动
                </div>
              )}
            </div>
          )}
        </ScrollArea>

        <div className="flex justify-between items-center pt-4 border-t">
          <p className="text-sm text-muted-foreground">
            已关联 {linkedActivityIds.size} 个活动
          </p>
          <Button variant="outline" onClick={() => setOpen(false)}>
            关闭
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
};
